import {
  SET_DEPARTMENTS_NAME,
  SET_SHOWN_DEPARTMENT_NAME,
  SET_SHOWN_QUEUE,
  UPDATE_TIME,
  UPDATE_DEPARTMENT,
  CHANGE_QUEUE_VIEW
} from "./actionType";

const setDepartmentsName = departments => ({
  type: SET_DEPARTMENTS_NAME,
  payload: departments
});

const setShownDepartment = (departmentName, data) => ({
  type: SET_SHOWN_DEPARTMENT_NAME,
  departmentName,
  data
});

const setShownQueue = queueId => ({
  type: SET_SHOWN_QUEUE,
  queueId
});

const updateTime = () => ({
  type: UPDATE_TIME
});

const updateDepartment = data => ({
  type: UPDATE_DEPARTMENT,
  data
});

const changeQueueView = viewBool => ({
  type: CHANGE_QUEUE_VIEW,
  viewBool
});

export {
  setDepartmentsName,
  setShownDepartment,
  setShownQueue,
  updateTime,
  updateDepartment,
  changeQueueView
};
