import React from "react";
import ListTableNames from "./ListTableNames";
import ListTableData from "./ListTableData";
import { setShownQueue } from "../../../../Redux/actions/actions";
import { queues } from "./listcol.module.scss";
import { connect } from "react-redux";

class ListCol extends React.Component {
  render() {
    if (Object.keys(this.props.queue).length < 1) {
      return <div className={queues}>Loading...</div>;
    }
    const names = this.props.queues.map(item => ({
      id: item.id,
      name: item.name
    }));

    return (
      <div className={queues}>
        <ListTableNames
          names={names}
          setShownQueue={this.props.setShownQueue}
        />
        <ListTableData />
      </div>
    );
  }
}

const mapState = state => ({
  shownQueue: state.shownQueue,
  queues: state.queues
});

const mapDispatch = dispatch => ({
  setShownQueue: queueId => dispatch(setShownQueue(queueId))
});

export default connect(mapState, mapDispatch)(ListCol);
