import React, { Component } from "react";
import Header from "./Header/Header";
import Container from "./Container";
import { connect } from "react-redux";
import { setDepartmentsName, updateDepartment } from "../Redux/actions/actions";

class Layout extends Component {
  componentDidMount() {
    const urlParams = new URLSearchParams(window.location.search);
    const BRANCH_ID = urlParams.get("branch") || 1;
    const socket = new WebSocket(
      `ws://10.0.22.36:8000/branches/${BRANCH_ID}/departments/`
    );
    socket.onmessage = response => {
      const DATA = JSON.parse(response.data);

      if (DATA.action === "init") {
        // console.log("TCL: Layout -> componentDidMount -> init", DATA.action);
        this.props.setDepartmentsName(DATA.payload);
      }
      if (DATA.action === "update_queue") {
        // console.log("TCL: Layout -> componentDidMount -> DATA", DATA);
      }
      if (DATA.action === "update_department") {
        this.props.updateDepartment(DATA.payload);
      }
    };
  }

  setShownDepartment = departmentName => {
    this.setState({
      shownDepartmentName: departmentName
    });
  };
  render() {
    return (
      <div>
        <Header />
        <Container />
      </div>
    );
  }
}

const getDispath = dispatch => {
  return {
    setDepartmentsName: deparment => dispatch(setDepartmentsName(deparment)),
    updateDepartment: date => dispatch(updateDepartment(date))
  };
};

export default connect(null, getDispath)(Layout);
