import React from "react";
import {
  sidebarItemClass,
  sidebarItemClass_active
} from "./Sidebar.module.scss";
import { setShownDepartment } from "../../Redux/actions/actions";
import { ReactComponent as XidmetLoqo } from "../../assets/img/icon/contact_support_24px_outlined.svg";
import { ReactComponent as SatishLoqo } from "../../assets/img/icon/card_travel_24px_outlined.svg";
import { ReactComponent as KassahLoqo } from "../../assets/img/icon/attach_money_24px_outlined.svg";
import { ReactComponent as HuquqLoqo } from "../../assets/img/icon/next_week_24px_outlined.svg";
import { ReactComponent as KassaHuquqLoqo } from "../../assets/img/icon/credit_card_24px_outlined.svg";
import { connect } from "react-redux";

function SidebarItem(props) {
  let settedIcon;
  switch (props.name) {
    case "Xidmet":
      settedIcon = <XidmetLoqo />;
      break;
    case "Satish":
      settedIcon = <SatishLoqo />;
      break;
    case "Kassa":
      settedIcon = <KassahLoqo />;
      break;
    case "Huquqi shexsler":
      settedIcon = <HuquqLoqo />;
      break;
    case "Huquqi shexsler - Kassa":
      settedIcon = <KassaHuquqLoqo />;
      break;
    default:
  }
  return (
    <div
      onClick={() => {
        document.title = props.name;
        props.setShownDepartment(props.name, props.departments);
      }}
      className={
        props.shownDepartmentName === props.name
          ? `${sidebarItemClass} ${sidebarItemClass_active}`
          : sidebarItemClass
      }
    >
      {settedIcon}
      <span
        style={{
          marginLeft: "5px"
        }}
      >
        {props.name}
      </span>
    </div>
  );
}

const mapProps = state => ({
  shownDepartmentName: state.shownDepartmentName,
  departments: state.departments
});

const mapDispath = dispatch => ({
  setShownDepartment: (departamentName, data) =>
    dispatch(setShownDepartment(departamentName, data))
});

export default connect(mapProps, mapDispath)(SidebarItem);
