import React from "react";
import CardPreloader from "./CardPreloader";
import ContentCard from "./ContentCard";
import { connect } from "react-redux";
import { updateTime } from "../../Redux/actions/actions";
import Tooltip from "../Tooltip/Tooltip";

const trueNames = {
  ticket_count: {
    name: "TICKETS",
    fullName: "Ticket count"
  },
  served_customer_count: {
    name: "SERVED",
    fullName: "Served customer count"
  },
  waiting_customer_count: {
    name: "WAITING",
    fullName: "Waiting customer count"
  },
  noshow_count: {
    name: "NO SHOW",
    fullName: "Noshow count"
  },
  reject_count: {
    name: "REJECT",
    fullName: "Reject count"
  },
  removed_customer_count: {
    name: "REMOVED",
    fullName: "Removed customer count"
  },
  avg_waiting_time: {
    name: "AWT",
    fullName: "Avg waiting time"
  },
  avg_serving_time: {
    name: "AST",
    fullName: "Avg serving time"
  },
  max_waiting_time: {
    name: "MWT",
    fullName: "Max waiting time"
  },
  max_serving_time: {
    name: "MST",
    fullName: "Max serving time"
  },
  first_ticket_time: {
    name: "FTT",
    fullName: "First ticket time"
  },
  last_ticket_time: {
    name: "LTT",
    fullName: "Last ticket time"
  }
};

const nestedData = [
  "max_serving_time",
  "avg_waiting_time",
  "avg_serving_time",
  "max_waiting_time"
];

class ContentCards extends React.Component {
  componentDidMount() {
    this.timer = setInterval(() => {
      this.props.updateTime();
    }, 1000);
  }

  componentWillUnmount() {
    clearInterval(this.timer);
  }
  render() {
    let preloader = [];
    let cardList = [];
    for (let [key, value] of Object.entries(
      this.props.showCardDataStore.showCardData
    )) {
      if (key === "id") {
      } else {
        if (nestedData.indexOf(key) !== -1) {
          let color = "normal";
          if (value.dinamic) {
            if (value.added !== null && value.added !== undefined) {
              let dfstring = value.added.split(":");
              if (key === "avg_waiting_time") {
                if (dfstring[0] > 0) {
                  color = "wrap-red";
                } else if (dfstring[1] > 0) {
                  color = "wrap-red";
                } else if (dfstring[1] < 40 && dfstring[1] > 20) {
                  color = "wrap-yellow";
                } else if (dfstring[1] < 20 && dfstring[1] > 0) {
                  color = "wrap-green";
                }
              } else {
                if (dfstring[0] > 0) {
                  color = "wrap-red";
                } else if (dfstring[1] > 20) {
                  color = "wrap-red";
                } else if (dfstring[1] < 20 && dfstring[1] > 10) {
                  color = "wrap-yellow";
                } else if (dfstring[1] < 10 && dfstring[1] > 0) {
                  color = "wrap-green";
                }
              }
            }
          }
          const valueReg = value.dinamic
            ? value.added === null
              ? value.text
              : value.added
            : value.text;
          cardList.push(
            <div className={`card-wrap ${color}`} key={key}>
              <Tooltip
                followCursor
                hideArrow
                trigger="hover"
                tooltip={trueNames[`${key}`].fullName}
              >
                <ContentCard name={trueNames[`${key}`].name} count={valueReg} />
              </Tooltip>
            </div>
          );
        } else {
          cardList.push(
            <div className="card-wrap" key={key}>
              <Tooltip
                followCursor
                hideArrow
                trigger="hover"
                tooltip={trueNames[`${key}`].fullName}
              >
                <ContentCard name={trueNames[`${key}`].name} count={value} />
              </Tooltip>
            </div>
          );
        }
      }
    }
    for (let i = 0; i < 12; i++) {
      preloader.push(<CardPreloader key={i} />);
    }
    if (Object.keys(this.props.showCardDataStore.showCardData).length > 0) {
      return (
        <div>
          <h1
            style={{
              margin: "0 0 15px -15px"
            }}
          >
            Total
          </h1>
          <div
            style={{
              display: "flex",
              flexWrap: "wrap",
              justifyContent: "space-between",
              margin: "0 -15px"
            }}
          >
            {cardList}
          </div>
        </div>
      );
    } else {
      return (
        <div
          style={{
            display: "flex",
            flexWrap: "wrap",
            justifyContent: "space-between"
          }}
        >
          {preloader}
        </div>
      );
    }
  }
}

const mapStore = store => ({
  departments: store.departments,
  departmentName: store.shownDepartmentName,
  showCardDataStore: store
});

const mapDispatch = dispatch => ({
  updateTime: () => dispatch(updateTime())
});
export default connect(mapStore, mapDispatch)(ContentCards);
