import React, { Component } from "react";
import { Provider } from "react-redux";
import thunkMiddleware from "redux-thunk";
import { createStore, applyMiddleware, compose } from "redux";
import loggerMiddleware from "./Redux/middleware/logger";
import monitorReducerEnhancer from "./Redux/enhancers/monitorReducer";
import rootReducer from "./Redux/reducers/departmentReducers";
import Layout from "./components/Layout";
import { composeWithDevTools } from "redux-devtools-extension";

const middlewareEnhancer = applyMiddleware(loggerMiddleware, thunkMiddleware);
const composedEnhancers = compose(middlewareEnhancer, monitorReducerEnhancer);
const store = createStore(
  rootReducer,
  undefined,
  composeWithDevTools(composedEnhancers)
);

class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <Layout />
      </Provider>
    );
  }
}

export default App;
