import {
  SET_DEPARTMENTS_NAME,
  SET_SHOWN_DEPARTMENT_NAME,
  SET_SHOWN_QUEUE,
  UPDATE_TIME,
  UPDATE_DEPARTMENT,
  CHANGE_QUEUE_VIEW
} from "../actions/actionType";

const expectDesc = [
  "name",
  "vacation_count",
  "vacations",
  "displacements_from",
  "displacements_to",
  "displacement_to_count",
  "displacement_from_count",
  "fte",
  "online_user_count",
  "queues"
];

const trueNames = {
  ticket_count: {
    name: "TICKETS",
    fullName: "Ticket count"
  },
  served_customer_count: {
    name: "SERVED",
    fullName: "Served customer count"
  },
  waiting_customer_count: {
    name: "WAITING",
    fullName: "Waiting customer count"
  },
  noshow_count: {
    name: "NO SHOW",
    fullName: "Noshow count"
  },
  reject_count: {
    name: "REJECT",
    fullName: "Reject count"
  },
  removed_customer_count: {
    name: "REMOVED",
    fullName: "Removed customer count"
  },
  avg_waiting_time: {
    name: "AWT",
    fullName: "Avg waiting time"
  },
  avg_serving_time: {
    name: "AST",
    fullName: "Avg serving time"
  },
  max_waiting_time: {
    name: "MWT"
  },
  max_serving_time: {
    name: "MST",
    fullName: "Max serving time"
  },
  first_ticket_time: {
    name: "FTT",
    fullName: "First ticket time"
  },
  last_ticket_time: {
    name: "LTT",
    fullName: "Last ticket time"
  }
};

const nestedData = [
  "max_serving_time",
  "avg_waiting_time",
  "avg_serving_time",
  "max_waiting_time"
];

const initialState = {
  branchName: "",
  isInit: false,
  branchId: "",
  departmentsName: [],
  departments: [],
  shownDepartmentName: "Xidmet",
  queues: [],
  shownQueue: null,
  showCardData: {},
  shownQueueData: {},
  shownQueueTableData: {},
  showTableView: false
};

export default function(state = initialState, action) {
  switch (action.type) {
    case SET_DEPARTMENTS_NAME:
      const ourDepartamentArrInit = action.payload.departments.filter(
        item => item.name === "Xidmet"
      );
      let ourDepartamentinit = {},
        ourQueuesInit = ourDepartamentArrInit[0].queues[0];
      let ourQueuesTableInit = ourDepartamentArrInit[0].queues;
      if (ourDepartamentArrInit.length > 0) {
        for (let [key, value] of Object.entries(ourDepartamentArrInit[0])) {
          if (expectDesc.indexOf(key) !== -1) {
          } else {
            if (nestedData.indexOf(key) !== -1) {
              ourDepartamentinit[`${key}`] = value;
              ourDepartamentinit[`${key}`]["added"] = null;
            } else {
              ourDepartamentinit[`${key}`] = value;
            }
          }
        }
      }
      return {
        ...state,
        departmentsName: action.payload.departments.map(item => item.name),
        departments: action.payload.departments,
        branchName: action.payload.branch.name,
        showCardData: ourDepartamentinit,
        shownQueueData: ourQueuesInit,
        shownQueueTableData: ourQueuesTableInit,
        queues: ourDepartamentArrInit[0].queues
      };
    case SET_SHOWN_DEPARTMENT_NAME:
      const ourDepartamentArr = action.data.filter(
        item => item.name === action.departmentName
      );
      let ourDepartament = {};
      if (ourDepartamentArr.length > 0) {
        for (let [key, value] of Object.entries(ourDepartamentArr[0])) {
          if (expectDesc.indexOf(key) !== -1) {
          } else {
            ourDepartament[`${key}`] = {};
            if (nestedData.indexOf(key !== -1)) {
              ourDepartament[`${key}`]["added"] = null;
            }
            ourDepartament[`${key}`] = value;
          }
        }
      }
      let ourQueuesTable = ourDepartamentArr[0].queues;
      let shownQueueMod = ourDepartamentArr[0].queues[0];
      return {
        ...state,
        shownQueue: null,
        shownDepartmentName: action.departmentName,
        showCardData: ourDepartament,
        shownQueueData: shownQueueMod,
        shownQueueTableData: ourQueuesTable,
        queues: ourDepartamentArr[0].queues
      };
    case SET_SHOWN_QUEUE:
      let shownQueueDataMod = state.queues.filter(
        item => item.id === action.queueId
      );
      return {
        ...state,
        shownQueue: action.queueId,
        shownQueueData: { ...shownQueueDataMod[0] }
      };
    case UPDATE_TIME:
      if (Object.keys(state.shownQueueData).length < 1) {
        return {
          ...state
        };
      }
      let oldStates = state.showCardData,
        oldStateQueue = state.shownQueueData;

      let shownQueueTableDataMod = [];
      if (state.showTableView) {
        for (let [key, value] of Object.entries(oldStateQueue)) {
          if (nestedData.indexOf(key) !== -1) {
            if (value.dinamic) {
              let delta =
                Date.now() - new Date(value.calculation_time).getTime();
              let ms = delta * value.coefficient;
              let val = new Date(value.value + ms);
              let displayDate =
                val.getHours() +
                ":" +
                (val.getMinutes() < 10
                  ? "0" + val.getMinutes()
                  : val.getMinutes()) +
                ":" +
                (val.getSeconds() < 10
                  ? "0" + val.getSeconds()
                  : val.getSeconds());
              oldStateQueue[key].added = displayDate;
            }
          }
        }
      } else {
        shownQueueTableDataMod = state.shownQueueTableData.map(item => item);
        shownQueueTableDataMod.forEach((item, index) => {
          for (let [key, value] of Object.entries(item)) {
            if (nestedData.indexOf(key) !== -1) {
              if (value.dinamic) {
                let delta =
                  Date.now() - new Date(value.calculation_time).getTime();
                let ms = delta * value.coefficient;
                let val = new Date(value.value + ms);
                let displayDate =
                  val.getHours() +
                  ":" +
                  (val.getMinutes() < 10
                    ? "0" + val.getMinutes()
                    : val.getMinutes()) +
                  ":" +
                  (val.getSeconds() < 10
                    ? "0" + val.getSeconds()
                    : val.getSeconds());
                item[key].added = "";
                item[key].added = displayDate;
              }
            }
          }
        });
      }

      for (let [key, value] of Object.entries(oldStates)) {
        if (nestedData.indexOf(key) !== -1) {
          if (value.dinamic) {
            let delta = Date.now() - new Date(value.calculation_time).getTime();
            let ms = delta * value.coefficient;
            let val = new Date(value.value + ms);
            let displayDate =
              val.getHours() +
              ":" +
              (val.getMinutes() < 10
                ? "0" + val.getMinutes()
                : val.getMinutes()) +
              ":" +
              (val.getSeconds() < 10
                ? "0" + val.getSeconds()
                : val.getSeconds());
            oldStates[key].added = displayDate;
          }
        }
      }
      if (state.showTableView) {
        return {
          ...state,
          showCardData: oldStates,
          shownQueueData: oldStateQueue
        };
      } else {
        return {
          ...state,
          showCardData: oldStates,
          shownQueueTableData: shownQueueTableDataMod
        };
      }

    case UPDATE_DEPARTMENT:
      const departmentsMod = state.departments.map(item => {
        if (item.id === action.data.id) {
          return {
            ...item,
            ...action.data
          };
        } else {
          return { ...item };
        }
      });

      if (state.showCardData.id === action.data.id) {
        let showCardDataMod = {};
        showCardDataMod = {
          ...state.showCardData,
          ...action.data
        };
        return {
          ...state,
          departments: departmentsMod,
          showCardData: showCardDataMod
        };
      }
      return {
        ...state,
        departments: departmentsMod
      };
    case CHANGE_QUEUE_VIEW:
      return {
        ...state,
        showTableView: action.viewBool
      };
    default:
      return state;
  }
}
