import React from "react";
import { SidebarNavClass } from "./Sidebar.module.scss";
import SidebarItem from "./SidebarItem";
import { connect } from "react-redux";


function SidebarNav(props) {
  return (
    <div className={SidebarNavClass}>
      {props.departmentsName.map((item, index) => {
        return (
          <SidebarItem
            key={index}
            name={item}
          />
        )
      })}
    </div>
  );
}

const getStore = state => {
  return {
    departmentsName: state.departmentsName
  }
}


export default connect(getStore)(SidebarNav)