import React from "react";
import { queues_name, queues_title, queues_list } from "./listcol.module.scss";

export default function ListTableNames(props) {
  const names = props.names.map(item => (
    <li key={item.id} id={item.id} onClick={() => props.setShownQueue(item.id)}>
      {item.name}
    </li>
  ));
  return (
    <div className={queues_name}>
      <p className={queues_title}>Queue name</p>
      <ul className={queues_list}>{names}</ul>
    </div>
  );
}
