import React from "react";
import ListPreloader from "./ListPreloader";
import {
  contentlistveiw,
  contentlistveiw_header,
  contentlistveiw_tabs
} from "./content.module.scss";
import listIcon from "../../assets/img/icon/list_24px_outlined.png";
import tableIcon from "../../assets/img/icon/view_list_24px_outlined.png";
import ListTable from "./Listviews/ListTable/ListTable";
import ListCol from "./Listviews/ListCol/ListCol";
import { changeQueueView } from "../../Redux/actions/actions";
import { connect } from "react-redux";

function ContentList(props) {
  const { departments, shownDepartment } = props;
  const ourDepartamentArr = departments.filter(
    item => item.name === shownDepartment
  );
  let queues = [];
  if (ourDepartamentArr.length > 0) {
    queues = ourDepartamentArr[0].queues;
  }

  if (
    queues.length > 0 &&
    Object.keys(props.currQueues.shownQueueData).length > 0
  ) {
    return (
      <div className={contentlistveiw}>
        <div
          className="header-wrap"
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "space-between",
            marginBottom: "25px"
          }}
        >
          <div className="header-name">
            <div
              className="header-title"
              style={{
                fontSize: "28px",
                fontWeight: "bold"
              }}
            >
              Queues
            </div>
          </div>
          <div className={contentlistveiw_header}>
            <div className={contentlistveiw_tabs}>
              <div
                className="list-btn"
                style={{
                  cursor: "pointer"
                }}
                onClick={() => props.changeQueueView(false)}
              >
                <img src={tableIcon} alt="" />
              </div>
              <div
                className="list-btn"
                style={{
                  cursor: "pointer",
                  marginRight: "4px"
                }}
                onClick={() => props.changeQueueView(true)}
              >
                <img src={listIcon} alt="" />
              </div>
            </div>
          </div>
        </div>

        <div
          style={{
            height: "100%"
          }}
        >
          {props.showTableView ? (
            <ListCol
              defaultId={queues[0].id}
              queue={props.currQueues.shownQueueData}
            />
          ) : (
            <ListTable />
          )}
        </div>
      </div>
    );
  } else {
    return <ListPreloader />;
  }
}

const mapState = state => ({
  departments: state.departments,
  shownDepartment: state.shownDepartmentName,
  currQueues: state,
  showTableView: state.showTableView
});

const mapDispatch = dispatch => ({
  changeQueueView: viewBool => dispatch(changeQueueView(viewBool))
});
export default connect(mapState, mapDispatch)(ContentList);
