import React from "react";
import { queues_data, queues_title, queues_list } from "./listcol.module.scss";
import { connect } from "react-redux";

const trueNames = {
  ticket_count: "TICKETS",
  served_customer_count: "SERVED",
  waiting_customer_count: "WAITING",
  noshow_count: "NO SHOW",
  reject_count: "REJECT",
  removed_customer_count: "REMOVED",
  avg_waiting_time: "AWT",
  avg_serving_time: "AST",
  max_waiting_time: "MWT",
  max_serving_time: "MST",
  first_ticket_time: "FTT",
  last_ticket_time: "LTT"
};
const nestedData = [
  "max_serving_time", //max_serving_time
  "avg_waiting_time", //avg_waiting_time
  "avg_serving_time", //avg_serving_time
  "max_waiting_time" //max_waiting_time
];

function ListTableData(props) {
  let table = [];
  for (let [key, value] of Object.entries(props.queues.shownQueueData)) {
    if (key === "id" || key === "name") {
    } else {
      table.push(
        <li key={key}>
          <span>{trueNames[`${key}`]}</span>
          <span>
            {nestedData.indexOf(key) !== -1
              ? value.dinamic
                ? value.added !== null
                  ? value.added
                  : value.text
                : value.text
              : value === null
              ? "no data"
              : value}
          </span>
        </li>
      );
    }
  }

  return (
    <div className={queues_data}>
      <div className={queues_title}>{props.queues.shownQueueData.name}</div>
      <ul className={queues_list}>{table.map(item => item)}</ul>
    </div>
  );
}

const mapData = state => ({
  queues: state
});

export default connect(mapData)(ListTableData);
