import React from "react";
import { table, table_head, table_body } from "./liststyle.module.scss";
import { connect } from "react-redux";
import CommonTooltip from "../../../Tooltip/CommonTooltip";

const nestedData = [
  "max_serving_time",
  "avg_waiting_time",
  "avg_serving_time",
  "max_waiting_time"
];

function ListTable(props) {
  const {
    tableData: { shownQueueTableData: queues }
  } = props;
  let tableRows = queues.map((item, index) => {
    let inner = [];
    for (let [key, value] of Object.entries(item)) {
      if (key === "id") {
      } else if (nestedData.includes(key)) {
        let color = "green";
        if (value.dinamic) {
          if (value.added !== null && value.added !== undefined) {
            let dfstring = value.added.split(":");
            if (key === "avg_waiting_time") {
              if (dfstring[0] > 0) {
                color = "red";
              } else if (dfstring[1] > 0) {
                color = "red";
              } else if (dfstring[1] < 40 && dfstring[1] > 20) {
                color = "yellow";
              } else if (dfstring[1] < 20 && dfstring[1] > 0) {
                color = "green";
              }
            } else {
              if (dfstring[0] > 0) {
                color = "red";
              } else if (dfstring[1] > 20) {
                color = "red";
              } else if (dfstring[1] < 20 && dfstring[1] > 10) {
                color = "yellow";
              } else if (dfstring[1] < 10 && dfstring[1] > 0) {
                color = "green";
              }
            }
          }
        }
        inner.push(
          <td key={key}>
            <span
              style={{
                padding: "0 6px",
                borderRadius: "4px",
                color: color
              }}
            >
              {value.dinamic
                ? value.added === null
                  ? value.text
                  : value.added
                : value.text}
            </span>
          </td>
        );
      } else if (key === "name") {
        inner.push(<td key={key}>{value}</td>);
      } else {
        inner.push(
          <td
            key={key}
            style={{
              textAlign: "center"
            }}
          >
            <span
              style={{
                padding: "0 6px",
                borderRadius: "4px",
                textAlign: "center",
                color: "#333"
              }}
            >
              {value === null ? "no ticket" : value}
            </span>
          </td>
        );
      }
    }
    return (
      <tr key={index}>
        {inner.map(item => {
          return item;
        })}
      </tr>
    );
  });

  return (
    <div
      style={{
        overflow: "auto",
        maxWidth: "calc(100vw - 260px)",
        borderRadius: "5px"
      }}
    >
      <table className={table}>
        <thead className={table_head}>
          <tr>
            <td>Queues</td>
            <td>Ticket&nbsp;Count</td>
            <td>
              <CommonTooltip trigger="hover" tooltip="Served customer count">
                Served
              </CommonTooltip>
            </td>
            <td>
              <CommonTooltip trigger="hover" tooltip="Waiting customer count">
                WAITING
              </CommonTooltip>
            </td>
            <td>
              <CommonTooltip trigger="hover" tooltip="Noshow count">
                NO&nbsp;SHOW
              </CommonTooltip>
            </td>
            <td>
              <CommonTooltip trigger="hover" tooltip="Reject count">
                REJECT
              </CommonTooltip>
            </td>
            <td>
              <CommonTooltip trigger="hover" tooltip="Removed customer count">
                REMOVED
              </CommonTooltip>
            </td>
            <td>
              <CommonTooltip trigger="hover" tooltip="Avg waiting time">
                AWT
              </CommonTooltip>
            </td>
            <td>
              <CommonTooltip trigger="hover" tooltip="Avg serving time">
                AST
              </CommonTooltip>
            </td>
            <td>
              <CommonTooltip trigger="hover" tooltip="Max waiting time">
                MWT
              </CommonTooltip>
            </td>
            <td>
              <CommonTooltip trigger="hover" tooltip="Max serving time">
                MST
              </CommonTooltip>
            </td>
            <td>
              <CommonTooltip trigger="hover" tooltip="First ticket time">
                FTT
              </CommonTooltip>
            </td>
            <td>
              <CommonTooltip trigger="hover" tooltip="Last ticket time">
                LTT
              </CommonTooltip>
            </td>
          </tr>
        </thead>
        <tbody className={table_body}>{tableRows}</tbody>
      </table>
    </div>
  );
}

const mapState = state => ({
  tableData: state
});

export default connect(mapState)(ListTable);
